.SideConcepts{
  position: absolute;
  top: 5%;
  width: 100%;
  height: 95%;
  margin: 0%;
  }
.uparrow{
  display: none;
}
.Class-UP i{
  display: none;
}
.Class-DOWN i{
  display: none;
}
  .side-bar{
    width: 90%;
    height: 90%;
    margin: 5%;
    overflow: hidden;
    overflow-y: scroll;
    /* border: 1px solid white; */
  }
  .TopConcepts{
    width: 100%;
    height: 50%;
    /* border: 1px solid white; */
  }
  .AlgoName{
    width: 100%;
    height: 20%;
    /* border: 1px solid white; */
  }
  .AlgoName p{
    color:#7D0AF8;
    float: left;
    margin: 2% 2%;
    font-size: 24px;
    font-family:cursive;
    text-shadow: 0px 0px 10px rgba(125 , 10, 248, 1);
  }

  .AlgoAbout{
    width: 100%;
    height: 30%;
    /* border: 1px solid white; */
  }
  .AlgoAbout > p{
    color:whitesmoke;
    margin: 2% 2%;
    font-size: 15px;
    font-family:monospace;
    box-sizing: border-box;
    text-align: left;
  }

  .AlgoTimeCompTitle{
    width: 100%;
    height: 20%;
    /* border: 1px solid white; */
  }
  .AlgoTimeCompTitle > p{
    color:#7D0AF8;
    float: left;
    margin: 2% 2%;
    font-size: 17px;
    font-family:sans-serif;
    font-weight: 600;
  }

  .AlgoTimeComp{
    width: 100%;
    height: 30%;
    /* border: 1px solid white; */
  }
  .AlgoTimeComp > p{
    text-align: left;
    margin: 0% 2%;
    font-size: 13px;
    padding: 0%;
    color:whitesmoke;
  }
  .AlgoTimeComp > p > span{
    text-align: left;
    margin: 0% 2%;
    font-size: 13px;
    padding: 0%;
    color:whitesmoke;
    font-weight: 600;    
  }






  .EndConcepts{
    width: 100%;
    height: 154%;
    /* border: 1px solid white; */
  }
  .ContentBoxTitle{
    width: 96%;
    height: 8%;
    /* border: 1px solid white; */
    margin: 2%;
  }
  .ContentBoxTitle > p{
    color:#7D0AF8;
    float: left;
    margin: 3% 2%;
    font-size: 19px;
    font-family: cursive;
    font-weight: 500;
  }
  .ContentBox{
    width: 96%;
    height: 86%;
    border: none;
    background-color: #3d3b3b;
    box-shadow: 0px 0px 3px rgba(255, 255, 255, 1);
    margin: 2%;
    border-radius: 10px;
    
  }

  .ContentBox > p{
    color:whitesmoke;
    margin: 2% 2%;
    font-size: 15px;
    font-family:monospace;
    box-sizing: border-box;
    text-align: left;
    padding-top: 20px;
    box-sizing: border-box;
  }

  ::-webkit-scrollbar {
    width: 20px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #7D0AF8; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #5906b1; 
  }





@media(max-width:666px){
  .AlgoName p{
    font-size: 18px;
  }
  .AlgoAbout > p{
    font-size: 12px;
  }
  .AlgoTimeCompTitle > p{
    font-size: 13px;
  }
  .AlgoTimeComp > p{
    font-size: 11px;
  }
  .AlgoTimeComp > p > span{
    font-size: 11px;    
  }
  .ContentBox > p{
    font-size: 13px;
  }
}


@media(max-width:429px){
  .AlgoName p{
    font-size: 18px;
  }
  .AlgoAbout > p{
    font-size: 9px;
  }
  .AlgoTimeCompTitle > p{
    font-size: 13px;
  }
  .AlgoTimeComp > p{
    font-size: 9px;
  }
  .AlgoTimeComp > p > span{
    font-size: 9px;    
  }
  .ContentBox > p{
    font-size: 10px;
  }
}