.StopButtonClass{
  display: inline-block;
  width: 100%;
  height: 100%;
  margin-left: 0%;
  margin-top: -2px;
  padding: 0 65px 0 65px;
  border-radius: 7px;
  background-color: #fff;
  outline: none;
  border: none;
  font-weight: 600;
  color: #323232;
  cursor: pointer;
}
.reset-circle{
  display: none;
}

.StopButtonClass:hover{
  background-color: rgba(255, 0, 0, 0.5);
  font-weight: 600;
  color: #fff;
  box-shadow: 0px 0px 50px rgba(255, 0, 0, 0.5);
}

@media(max-width: 1200px){
  .reset-circle{
    display: block;
    margin-top: -5px;
    font-size: 54px;
    color: #fff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .reset-circle-inside{
    display: block;
    margin-top: -3px;
    font-size: 55px;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    transition: all 0.3s ease 0s;
  }
  .reset-circle-inside:hover{
    color: rgba(255, 0, 0, 0.5);
    transform: translateY(-7px);
  }
  .StopButtonClass p{
    display: none;
  }
  .StopButtonClass{
    background-color: #323232;
  }
  .StopButtonClass:hover{
    background-color: #323232;
    box-shadow: 0px 0px 0px;
  }
}

@media(max-width: 429px){
  .reset-circle-inside{
    font-size: 35px;
  }
}