.PlayButtonClass{
    border: 1px solid #fff;
    display: inline-block;
    width: 100%;
    height: 100%;
    margin-left: 0%;
    margin-top: -2px;
    padding: 0 65px 0 65px;
    border-radius: 7px;
    background-color: #fff;
    outline: none;
    border: none;
    font-weight: 600;
    color: #323232;
    cursor: pointer;
  }

  .PlayButtonClass:hover{
    background-color: rgba(4, 162, 49, 0.768);
    font-weight: 600;
    color: #fff;
    box-shadow: 0px 0px 50px rgba(4, 162, 49, 0.768);
  }
  .play-circle{
    display: none;
  }
  
@media(max-width: 1200px){
  .PlayButtonClass{
    background-color: #323232;
    width: 50%;
    height: 96%;
    margin: 1px 1px ;
    padding: 0 65px 0 65px;
  }
  .PlayButtonClass:hover{
    background-color: #323232;
    box-shadow: 0px 0px 0px;
  }
  .PlayButtonClass p{
    display: none;
  }
  .play-circle{
    display: block;
    color: #fff;
    /* border: 1px solid black; */
    width: 100px;
    height: 50px;
    margin: 0px 0px;
    margin-left: -53px;
  }
  .play-circle-inside{
    display: block;
    margin-top: -3px;
    font-size: 55px;
    color: #fff;
    font-weight: 600;
    /* margin-left: -25px; */
    text-transform: uppercase;
    transition: all 0.3s ease 0s;
  }
  .play-circle-inside:hover{
    color: rgba(4, 162, 49, 1);
    transform: translateY(-7px);
  }
}

@media(max-width: 429px){
  .play-circle-inside{
    font-size: 35px;
  }
}
