*{
  box-sizing: border-box;
}
.App {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: #323232;
}

/* UpperBar */
.MainTitle{
  /* border: 1px solid white; */
  position: absolute;
  width: 20%;
  height: 10%;
  left: 0%;
  top:2%;
  z-index: 120;
}
.MainTitle p{
  color: white;
  font-size: 1.8rem;
  font-weight: 800;
  font-family: cursive;
}
.UpperBar{
  position: fixed;
  width: 100%;
  height: 15vh;
  background-color: #323232;
  z-index: 100;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 1);
  /* border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px; */
}

.ArraySize{
  display: inline-block;
  width: 300px;
  height: 92%;
  margin: 4px 6px;
  float: right; 
}

.Algorithm{
  display: inline-block;
  width: 200px;
  height: 92%;
  margin: 4px 6px;
  float: right;
}

.SortingSpeed{
  display: inline-block;
  width: 220px;
  height: 92%;
  margin: 4px 6px; 
  float: right;
}

.Stop{
  display: inline-block;
  width: 200px;
  height: 92%;
  margin: 4px 6px;
  float: right;
}
.Play{
  display: inline-block;
  width: 200px;
  height: 92%;
  margin: 4px 6px;  
  float: right;
}



.CommonDev-ArraySize,.CommonDev-Algorithm, .CommonDev-SortingSpeed, .CommonDev-Play, .CommonDev-Stop{
  /* display: inline-block; */
  width: 100%;
  height: 60%;
  margin-top: 30px;
  position: relative;
  top:0px;
}



/* Middle */

.MainContant{
  position: absolute;
  /* border: 1px solid black; */
  width: 100vw;
  height: 85vh;
  margin-top: 15vh;
  background-color: #323232;
}

/* {MainContant-1} */

.MainContant-1{
  position: absolute;
  top: 0%;
  /* border: 1px solid black; */
  width: 50%;
  height: 100%;
  left: 0%;
  background-color: #323232;
}
.ContantInside{
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid #3d3b3b; */
  width: 84%;
  height: 80%;
  margin: 10% 8%;
  background-color:#3d3b3b;
  border-radius: 10px;
  box-shadow: 0px 0px 5px rgba(255, 255, 255, 1);
  overflow: hidden;
}


/* {MainContant-2}  */

.MainContant-2{
  position: absolute;
  /* border: 1px solid black; */
  width: 50%;
  height: 100%;
  right: 0%;
  background-color: #323232;
}

.MainBetween{
  display: none;
}
.OpenAlgoBar{
  display: none;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 500;
  top: 0;
  left: 0;
  background-color: #323232;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px; 
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 1);
}
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}
.sidenav a:hover {
  color: #f1f1f1;
}
.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

.TopTitle{
  display: none;
}

.OpenAlgoBar-Down{
  display: none; 
}
.ThreeArrow{
  display: none;
}

@media(max-width: 1450px){
  .ArraySize{
    width: 250px;
  }
  
  .Algorithm{
    width: 180px;
  }
  
  .SortingSpeed{
    width: 190px;
  }
  
  .Stop{
    width: 180px;
  }
  .Play{
    width: 180px;
  }

}
