@media(max-width: 1200px){
  .MainTitle p{

    font-size: 1.9rem;
  }
  .MainTitle{
    /* border: 1px solid white; */
    width: 30%;
    height: 10%;
  }
  
  .MainContant{
    height: 85vh;
  }
  .MainContant-1{
    /* display: none; */
    width: 90%;
    margin: 3% 5%;
    height: 90%;
  }
  .ContantInside{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0% 8%;
    overflow: hidden;
  }
  .MainBetween{
    display: block;
    position: absolute;
    width: 100%;
    height: 13%;
    top:90%;
    background-color: #323232;
  }




  .OpenAlgoBar{
    position: relative;
    display: block;
    /* border: 1px solid white; */
    width: 35px;
    height: 50%;
    float: right;
    margin-top: 100px;
    border-radius: 10px 0px 0px 10px;
    box-shadow: 0px 0px 5px rgba(255, 255, 248, 0.5);
    z-index: 500;
  }
  .Span-1{
    position:absolute;
    right: 0%;
    width: 50%;
    height: 100%;
    color: rgba(255, 10, 248, 0.5);
    font-weight: 600;
    padding-right: 3px;
  }
  .Span-2{
    position:absolute;
    left: 0%;
    width: 50%;
    height: 100%;
  }
  .Span-2-left{
    position:absolute;
    left: 0%;
    top: 45%;
    width: 100%;
    height: 10%;
  }
  .Span-2-left i{
    align-items: center;
    color: white;
    font-size: 24px;
    cursor: pointer;
  }
  .Span-2-right{
    display: none;
    position:absolute;
    left: 0%;
    top: 45%;
    width: 100%;
    height: 10%;
  }
  .Span-2-right i{
    align-items: center;
    color: white;
    font-size: 24px;
    cursor: pointer;
  }
  .ThreeArrow{
    display: none;
  }
  .ThreeArrow span{
    display: none;
  }






  .MainContant-2{
    display: none;
    top: 2%;
    width: 80%;
    height: 98%;
    margin: 0% 10%;
    bottom: 100%;
    text-transform: uppercase;
    transition: all 0.3s ease 0s;
    box-sizing: border-box;
    }
    .Play-1200{
      display: inline-block;
      position: relative;
      width: 150px;
      height: 92%;
      /* left: 520px; */
      top: 4%;
    }
    .Stop-1200{
      display: inline-block;
      position: relative;
      width: 150px;
      height: 92%;
      /* left: 370px; */
      top: 4%;
    }
    
    .Play{
      display: none;
    }
    .Stop{
      display: none;
    }
    
    .CommonDev-Play, .CommonDev-Stop{
      /* display: inline-block; */
      position: relative;
      width: 100%;
      height: 100%;
      margin-top: 0px;
      position: relative;
      top:0px;
    }
}

