.range{
    border: 1px solid #fff;
    display: inline-block;
    width: 100%;
    height: 100%;
    /* margin-top: 30px; */
    padding: 0 65px 0 45px;
    border-radius: 7px;
    background-color: #fff;
  }
  .sliderValue{
    position: relative;
    width: 100%;
  }
  .sliderValue > span{
    position: absolute;
    height: 35px;
    width:35px;
    transform: translateX(-50%) scale(0);
    transform-origin: bottom;
    transition: transform 0.3s ease-in-out;
    font-weight: 500;
    font-size: 10px;
    top: -35px;  
    color: #fff;
    line-height: 40px;
    z-index: 2;
    padding: 2%;
  } 
  .sliderValue > span.ShowSpeedSpan{
    transform:translateX(-50%) scale(1);
  }
  .sliderValue > span::after{
    position: absolute;
    content: "";
    height: 35px;
    width:35px;
    background-color: #323232;
    left: 50%;
    color: #fff;
    transform: translateX(-50%) rotate(45deg) ;
    z-index: -1;
    border-bottom-left-radius: 50%;
    border-top-right-radius: 50%;
    border-top-left-radius: 50%;
    border: 2px solid #fff;
  } 
  .field{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .field > input{
    -webkit-appearance: none;
    height: 3px;
    width:100%;
    background:#ddd;
    border-radius: 5px;
    outline: none;
    border: none;
  }
  .field > input::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    background: #323232;
    border-radius: 50%;
    border: 1px solid #323232;
    cursor: pointer;
  }
  .field > input::-moz-range-progress{
    background: #323232;
  }
  .value{
    position: absolute;
    font-size: 18px;
    font-weight: 600;
    color: #323232;
  }
  .left{
    left:-43px;
  }
  .right{
    right:-43px;
  }