.SelectAlgorithm{
    border: 1px solid #fff;
    display: inline-block;
    width: 100%;
    height: 100%;
    margin-top: -2px;
    padding: 0 20px 0 45px;
    border-radius: 7px;
    background-color: #fff;
    outline: none;
    border: none;
    font-weight: 600;
    color: #323232;
    cursor: pointer;
}

.SelectAlgorithm > option{
    font-weight: 600;
    color: #323232;
    cursor: pointer;
}